.form-block {
  padding: 20px;
  position: fixed;
  inset: 0;
  cursor: pointer;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.67);
  z-index: 100;
  display: none;
}
.form-block__container {
  position: relative;
  max-width: 780px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 50px 100px;
  background-color: #f2f0f1;
  cursor: default;
  font-size: 20px;
  color: #000;
  text-align: center;
  border-radius: 20px;
}
.form-block__title {
  font-size: 30px;
  margin-bottom: 30px;
}
.form-block__subtitle {
  white-space: nowrap;
}
.form-block__subtitle_orange {
  color: #ff8a00;
  margin-bottom: 20px;
  font-weight: 500;
}
.form-block__subtitle_fieldset {
  margin-bottom: 15px;
  color: #282626;
}
.form-block__close {
  width: 22px;
  height: 22px;
  position: absolute;
  top: 20px;
  right: 25px;
  color: #282626;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.form-block__close:hover, .form-block__close:focus {
  color: #5b5656;
}

.contacts {
  display: flex;
  gap: 45px;
  justify-content: center;
  font-weight: 500;
  margin-bottom: 60px;
}
.contacts__icon {
  vertical-align: middle;
  color: #ff8a00;
  margin-right: 14px;
}
.contacts__list:not(:last-child) {
  margin-bottom: 10px;
}

.form__fieldset {
  display: flex;
  gap: 20px;
  border: 0;
  max-width: 280px;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 34px;
  color: #282626;
}
.form__input {
  display: block;
  width: 280px;
  height: 40px;
  background-color: #fff;
  border: 0;
}
.form__label {
  display: block;
}
.form__label-text {
  font-size: 12px;
  line-height: 14px;
  text-align: start;
}
.form__label:not(:last-child) {
  margin-bottom: 20px;
}
.form__button {
  width: 220px;
  height: 40px;
  background-color: #282626;
  color: #ff8a00;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
}

.form-block-btn {
  display: block;
  min-width: 80px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  margin-left: auto;
  color: #ff8a00;
}

.menu__wrapper {
  max-width: 394px;
  min-height: 394px;
  top: -100%;
  background-color: #f2f0f1;
  position: absolute;
  border-bottom: 10px solid #ff8a00;
  transition: 0.4s ease-in-out;
}
.menu__wrapper.active {
  z-index: 30;
  transition: 0.4s ease-in-out;
  top: 120px;
}
.menu__wrapper:before {
  content: "";
  display: block;
  position: absolute;
  background-image: url("../image/triangle.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 52px;
  height: 30px;
  top: -30px;
  left: 0;
}
.menu__list {
  padding-top: 70.5px;
}
.menu__item:not(:last-child) {
  border-bottom: 1px solid #ff8a00;
}
.menu__link {
  display: block;
  font-size: 20px;
  line-height: 30px;
  font-weight: 550;
  padding: 15px 20px 15px 19px;
  will-change: transform;
}